/*
n sides carousel
tan(π / n) = (l/2) / r
r = l / ( 2 tan(π/n) )
*/

// CSS attributes, Be careful, rotation (0deg) is meaningful!
const at_deg = (cmd, v) => `${cmd}(${vF(v)}deg)`;
const at_px  = (cmd, v) => `${cmd}(${vF(v)}px)`;
const atXY_deg = (cmd, cond, v) => `${cmd + (cond ? 'X' : 'Y')}(${vF(v)}deg)`;
const atXY_px  = (cmd, cond, v) => `${cmd + (cond ? 'X' : 'Y')}(${vF(v)}px)`;

//const sun_pos = getPosSun(new Date( /* '2019-05-21T18:00:00' */ ));  // { longitue, distance }
//console.log("Sun position:", sun_pos);

const getPosSunDEBUG = (msg="") => {
  const pos = getPosSun(/* new Date( '2019-05-21T18:00:00' ) */);
  console.log((new Date()).toLocaleString(), `sun(${msg}):`, pos);
  return pos;
}

// There exist two coordinate systems: Carousel-centered and Cell-centered
// =======================================================================
// Carousel-centered Coord.
// -----------------------------------------------------------------------
// X: tilt
// Y: rotation
// Z: 0 horizontal (left to right), 90deg vertical (to be a top to bottom order)
// =======================================================================
// Cell(Card)-centered Coord.
// -----------------------------------------------------------------------
// x: card face view angle (normal) should be adjusted wrt the Carousel tilt
// y: side flip
// z: rotate

const MODES = [
  "TABLE",
  "CAROUSEL"
];

const Card3D = props => {

  const initState = {  // default values
    mode  : 0,      // 0: TABLE, 1: CAROUSEL
    horiz : true,   // Horizontal or Vertical when Carousel mode
    heat  : true,   // heatmap
    zoom  : false,  // zoom on active card
    emoji : false,  // show emoji
    persp : 800,    // perspective
    tilto : -23.44, // axial tilt, obliquity (angle between rotational axis and its orbital axis)
    roti  : 0,      // rotation of the current cell index (that should come to the forefront)
    croty : 0,      // Card Rotation Y
    scale : 1.5,    // when in zoom on
    opact : .8,     // opacity
  };

  const [state, dispatch] = useReducer(reducer, initState);

  // Single Source of Truth
  // centralise the state handling logic, works in a similar way to Redux, but handles state on a local level.
  function reducer(state, action) {

    // toggle of the property between two values
    const toggle = (p, v1, v2) => (p !== v1) ? v1 : v2;

    // Generalized toggle (rotation) function in a sequence of values (array)
    const toggleSeq = (p, seq) => {  // arrow function should be defined inside not to occur Uncaught TypeError
      let id = seq.indexOf(p);
      id = (id < 0) ? 0 : (id + 1) % seq.length;
      return seq[id];
    }

    switch (action.type) { // Actions are payloads of information that send data from the application to the store.
      case 'reset':    return { ...initState, mode: state.mode };  // reset except mode (keep current mode)
      // 0: Grid 1: Carousel 2: Helix
      case 'togMode':  return { ...state, mode:  (state.mode + 1) % MODES.length };
      case 'togHoriz': return { ...state, horiz: !state.horiz   };
      case 'togHeat':  return { ...state, heat:  !state.heat    };
      case 'togEmoji': return { ...state, emoji: !state.emoji   };
      case 'togZoom':  return { ...state, zoom:  !state.zoom    };
      case 'setZoom':  return { ...state, zoom:  action.payload };
      // tilt of the carousel, (card rotation X when horiz)
      case 'chgTilt':  return { ...state, tilto: state.tilto + action.payload };  // change amount
      case 'togTilt':  return { ...state, tilto: toggleSeq(state.tilto, [0, -23.44, -60, -89]) };
      // Card Rotation Y
      case 'chgRoti':  return { ...state, roti:  state.roti + action.payload };
      case 'setRoti':  return { ...state, roti:  action.payload };
      // scale of the active card
      case 'chgScale': return { ...state, scale: state.scale + action.payload };
      case 'togScale': return { ...state, scale: toggle(state.scale, initState.scale, 5) };
      // perspective
      case 'togPersp': return { ...state, persp: toggleSeq(state.persp, [0, 100, 300, 600, 800, 1500, 4000]) };
      // opacity
      case 'chgOpact': return { ...state, opact: state.opact + action.payload };
      case 'togOpact': return { ...state, opact: toggleSeq(state.opact, [0, .4, .8, 1]) };
      // card rotation (Y when horiz)
      case 'togCRotY': return { ...state, croty: toggleSeq(state.croty, [0, 45, 90]) };

      default: throw new Error('Unexpected action');
    }
  }

  const [sunLon, setSunLon] = useState(() => getPosSunDEBUG("init").longitude);  // sun's longitude,
  // ✅ Lazy initial state: By passing function to the useState(), only called during the first render

  // carousel properties
  const cell_n  = props.items.length;          // number of cells

  // derived properties for calculations
  const bCrs     = (state.mode === 1);        // Don't need to be a funciton.
  const tilt     = state.horiz ? state.tilto : -state.tilto; // reverse sign when vertical mode (to lean on the right side)
  const cell_n_2 = cell_n / 2;              // mid point
  const d_cell   = deg360 / cell_n;            // angle width per each cell (card)
  const d_cell_2 = d_cell / 2;                 // half angle, center position of each cell
  const cell_s   = state.horiz ? props.cellWidth : props.cellHeight;  // offsetWidth
  const crs_r    = round( cell_s / ( 2 * tan(Math.PI / cell_n) ) );   // carousel radius
  // the adjustment of (rotation - .5) should be applied since the cell was center aligned
  // Do not normalize the rotation degree to find the shortest path
  const roty_crs = Number.parseFloat( (sunLon + (state.roti - .5) * d_cell).toFixed(2) ); // carousel's rotation degree
  //const rot_i   = deg => trunc( ( normB(deg + d_cell_2) / d_cell ) );
  const i_ff    = trunc( ( normB(roty_crs + d_cell_2) / d_cell ) ); // index of the forefront cell in range [0, cell_n]
  const dr_tilt = round( abs(sin(tilt * radians) * cell_s * .6) );  // expand crs_r not to be folded due to the tilt

  // layout
  const cols_table = cell_n / 6;  // columns in table mode
  const table_adj  = -2.5;  // table position adjustment (in cell width + margin unit)
  const yomi_min   = 90; // minimum width in px of the cell to show the yomi

  //*********************************** DEBUG OUTPUT
  //console.log("cell_s", cell_s, "roty_crs:", roty_crs, "crs_r", crs_r, "dr_tilt:", dr_tilt,
  //            "rotation:", state.roti, "i_ff:", i_ff);
  //*********************************** DEBUG OUTPUT

  // define dimensions, positions, and perspective
  const sceneStyle = {
    width:  props.cellWidth,
    height: props.cellHeight,
    '--pp': state.persp + 'px',
    '--cell_op': state.opact    // define CSS variables at the higher level DOM tree
  };

  // Encode rotation or Decode rotation of the carousel
  const carouselStyle = {
    //lineHeight: Math.round(props.cellHeight) + 'px',
    transform: at_px('translateZ', -crs_r)         +
               ( !state.horiz ? ' rotateZ(90deg)' : '' ) +
               at_deg(' rotateX', tilt)            +
               at_deg(' rotateY', -roty_crs)  // sun's location counted in
  };

  // make it free from the carousel's rotation, so as to be parallel to the screen (Reverse rotation)
  const innerFrontFacingStyle = {  // apply in the reverse order to that of the carousel
    '--sun_r': vF(cell_s/8) + 'px',
    transform: at_deg( 'rotateY', roty_crs) +
               at_deg(' rotateX', -tilt)   +
               ( !state.horiz ? ' rotateZ(-90deg)' : '' )  // adding translateZ here causes weird transition
  };

  // Precalculate this part out of the cell loop for performance, ENVIRONMENT (independent of cell id)
  const precalc_cell = (zff=false) =>
    bCrs ?
      at_px( ' translateZ', crs_r + (zff ? dr_tilt + 80 : dr_tilt) ) +
      ( !state.horiz ? ` rotateZ(-90deg)`                             : '' ) + // vertical mode: apply before tilt and cardRot
      ( tilt         ? atXY_deg(' rotate',  state.horiz, -tilt)       : '' ) + // cardRotX is not used as a control variable,
      ( state.croty  ? atXY_deg(' rotate', !state.horiz, state.croty) : '' ) + // totally linked to on the tilt.x
      ( zff          ? ` scale3d(${state.scale},${state.scale},${state.scale})` : '' )
    : // modes.TABLE
    ( zff ? ` translateZ(100px) scale(${state.scale})` : '' );

  // Initialize either cases, out of the loop
  const trans_cell_ENV     = precalc_cell();
  const trans_cell_ENV_zff = state.zoom ? precalc_cell(true) : '';  // No need to calculate when zoomOff

  // Cell coord. arrage the cells relative to the Carousel coord. 
  // To show the cell stand on the right angle we need to adjust the forefront cell's rotateY angle.
  // But for optimization, let's make this be independent on the Sun's location (that is already counted in
  // the Carousel coord.)
  const transformCell = i =>
    ( bCrs ?  // Cell coord.
      at_deg('rotateY', i * d_cell) :
  // table layout: columns for horiz (rows for vert), margin ratio
      atXY_px( 'translate',  state.horiz, ( table_adj +      (i % cols_table) ) * (props.cellWidth  * 1.2) ) +
      atXY_px(' translate', !state.horiz, ( table_adj + trunc(i / cols_table) ) * (props.cellHeight * 1.2) )
    ) +
  // common, precalculated transforms
    ( ( state.zoom && (i === i_ff) ) ? trans_cell_ENV_zff : trans_cell_ENV );

  // Optimize not to repeat the same portion of each cell
  // STEP 1. Build a template (out of the cell loop that is not dependent on i):
  // `rotateY(${i*15}deg) translateZ(388px) rotateX(23.5deg)`   -> should run only once per rendering
  // STEP 2. Generate string from the template with each cell
  /*****************************************************************
   * Cell Loop
   *****************************************************************/
  const crsCell = (item, i) => {  // Render each cell(card)
    
    const forefront = (i === i_ff);  // check if i is the index of the forefront cell

    // get the index difference for the shortest path
    // index: 0..(cell_n-1), output range should be [-m,+m], can be negative, meaning opposite rotational direction
    // shorter form is 50%~80% fater: for 2400 M calculations, it took 4.6 sec (using abs form) and 8.6 (if cond. form)
    // Oops! It was WRONG.  (m = cell_n/2) => ( m - abs(m - (i - i_ff)))
    const shortest_ipath = ( () => {
      let d = i - i_ff;     // if (abs(d) > m)  d += -sign(d) * cell_n;
      if (d < -cell_n_2)
        d += cell_n;
      else if (d > cell_n_2)
        d -= cell_n;
      return d;
    } )();  // IIFE

    /*
    // distance between two points in:0..23, out:0..12 when cell_n=24, then normalize 1..0
    const proximity = bCrs
                    ? 1 - ( abs(shortest_ipath) * cos(tilt * radians) * 2 / cell_n )
                    : 1;
    const proximity_range = (v_min, v_max) => v_min + round( (v_max - v_min) * proximity );
    */

    const onCardClick = () => {
      if (forefront)
        dispatch({type: 'togZoom'})   // toggle zoom when forefront
      else {
        if (!state.zoom)
          dispatch({type: 'togZoom'})   // when clicked, bring it to the forefront and zoom it always
        dispatch({type: 'setRoti', payload: state.roti + shortest_ipath}); // rotate to the clicked card via the shortest path
      }
    }

    const classSfx = !state.horiz ? "_v" : "";

    // 二十四節気card
    // 七十二候 3 per each sekki card、読み[1],名称[0]
    return (
      <div key={i} className={"crs_cell" + ( forefront ? " shadow_ff" : "" )}
           style={{  // cell style
             transform   : transformCell(i),
             //backfaceVisibility: forefront(i) ? 'hidden' : 'visible',
             zIndex      : forefront && 1000,  // undefined will not be copied
             //'--cell_cl' : proximity_range(64, 255), // color value (0..255) according to the distance from the ff cell
           }}
           onClick={()=>onCardClick()}
      >
        <div className={"sekki-name" + classSfx}
          style={{
            background: 'hsla(' +
              (state.heat ? (props.hue[i % props.hue.length] + ',100') : '0,0') + '%,50%,' + state.opact + ')'
            //proximity_range(10, 50) + '%,' + state.opact + ')'
          }}
        >
          {item}
          {state.emoji && <span className="sekki-emoji" role="img" aria-label="emoji">{sekki.s24emoji[i]}</span>}
        </div>
        <div>
          {sekki.s72[i].map((s, j) =>
            <div key={j} className={"s72-names" + classSfx}>
              {(cell_s > yomi_min) && <span className="s72-yomi">{s[1]}</span>}
              <br/>
              {s[0]}
            </div>)}
        </div>
      </div>
    );
  }

  /*
  const intervalRef = useRef();  // generic container whose current property is mutable
  useEffect( () => {
    const id = setInterval( () => {
      setSunLon(getPosSunDEBUG(id).longitude);  // You know, sunLon doesn't change here yet.
    }, 60*1000);  // every minute update sun's position and rerender
    intervalRef.current = id;
    console.log("useEffect()", id);
    return () => clearInterval(intervalRef.current);
  }, []);  // dependency list, [] fire only at the start
  */

  useInterval(() => {
      if (bCrs)
        setSunLon(getPosSunDEBUG().longitude);  // You know, sunLon doesn't change here yet.
    }, 60*1000);  // every minute update sun's position and rerender

  /*
  const handleKeyDown = ev => {
    console.log(ev);
    if (ev.keyCode === 38) {  // up arrow
      console.log('UP');
      state.horiz ? incTiltX(1) : incTiltY(1);
    } else if (ev.keyCode === 40) {  // down arrow
      console.log('DOWN');
      state.horiz ? incTiltX(-1) : incTiltY(-1);
    }
  }

  const move_xy = (dx, dy) => {
    const f = a => trunc(a/5);
    const m = state.horiz ? f(-dy) : f(dx);
    if (m !== 0)
      state.horiz ? incTiltX(m) : incTiltY(m);
    //incTiltY(dy);
  }
    <EventMove render={move_xy}>
          <InnerSpace width={400} height={400} cellHeight={props.cellHeight} />
  */

  return (
    <>
      <ControlCrs state={state} dispatch={dispatch} cell_n={cell_n} />
      <div className="crs_scene" style={sceneStyle}>
        <div className="crs_body" style={bCrs ? carouselStyle : undefined}>
          <div className="sun" style={bCrs ? innerFrontFacingStyle : {opacity: '.1'}}
              onClick={()=>dispatch({type: 'togHeat'})}
          />
          {props.items.map((s24, i) => crsCell(s24, i))}
        </div>
      </div>
    </>
  );
}

const ControlCrs = ({ state, dispatch, cell_n }) => {
  const degD = 5;  // degree
  const bCrs = (state.mode === 1);

  return (<div className="btn-container">
    <TestBtn onClick={()=>dispatch({type: 'reset'})}>Reset({window.innerWidth}x{window.innerHeight})</TestBtn>
    <TestBtn onClick={()=>dispatch({type: 'togMode'})}>Mode:{MODES[state.mode]}</TestBtn>
    <TestBtn onClick={()=>dispatch({type: 'togHoriz'})}>{state.horiz ? 'Horizontal' : 'Vertical'}</TestBtn>
    <TestBtn disabled={!bCrs}
             onClick={()=>dispatch({type: 'togPersp'})}>Perspective={state.persp}px</TestBtn>
    <TestBtn onClick={()=>dispatch({type: 'togHeat'})}>{state.heat ? 'Heatmap' : 'No Color'}</TestBtn>
    <TestBtn onClick={()=>dispatch({type: 'togZoom'})}>{state.zoom ? 'Zoom On' : 'Zoom Off'}</TestBtn>
    <TestBtn onClick={()=>dispatch({type: 'togEmoji'})}>{state.emoji ? 'emoji' : 'No emoji'}</TestBtn>
    <TestBtn disabled={state.roti === 0}
             onClick={()=>dispatch({type: 'setRoti', payload: 0})}>Rotate(Today)</TestBtn>
    <TestBtn onClick={()=>dispatch({type: 'chgRoti', payload: cell_n/4})}>Rotate(90°)</TestBtn>
    <TestBtn onClick={()=>dispatch({type: 'chgRoti', payload:  1})}>Rotate(+)</TestBtn>
    <TestBtn onClick={()=>dispatch({type: 'chgRoti', payload: -1})}>Rotate(-)</TestBtn>
    <TestBtn disabled={!bCrs}
             onClick={()=>dispatch({type: 'togTilt'})}>Toggle Tilt={vF(state.tilto)}°</TestBtn>
    <TestBtn disabled={!bCrs}
             onClick={()=>dispatch({type: 'chgTilt', payload:  degD})}>Tilt(+{degD}°)</TestBtn>
    <TestBtn disabled={!bCrs}
             onClick={()=>dispatch({type: 'chgTilt', payload: -degD})}>Tilt(-{degD}°)</TestBtn>
    <TestBtn disabled={!state.zoom}
             onClick={()=>dispatch({type: 'togScale'})}>Scale=x{state.scale}</TestBtn>
    <TestBtn disabled={!state.zoom}
             onClick={()=>dispatch({type: 'chgScale', payload:  .5})}>Scale(+)</TestBtn>
    <TestBtn disabled={!state.zoom || state.scale <= .5}
             onClick={()=>dispatch({type: 'chgScale', payload: -.5})}>Scale(-)</TestBtn>
    <TestBtn disabled={state.mode === 0}
             onClick={()=>dispatch({type: 'togCRotY'})}>Card Y={state.croty}°</TestBtn>
    <TestBtn onClick={()=>dispatch({type: 'togOpact'})}>Opacity={vF(state.opact,1)}</TestBtn>
    <TestBtn disabled={state.opact >= 1}
             onClick={()=>dispatch({type: 'chgOpact', payload:  .1})}>Opacity(+.1)</TestBtn>
    <TestBtn disabled={state.opact <= 0}
             onClick={()=>dispatch({type: 'chgOpact', payload: -.1})}>Opacity(-.1)</TestBtn>
    <input type="date" placeholder="" />
  </div>);
}

