const TestBtn = props => <button className="test-btn" {...props}>{props.children}</button>;

const App = () => {
  const ws = useWindowSize();
  const mw = [600, 900, 1200, 1800, 2400];  // maximum width to cover the group
  const mh = trunc( ws.h / 100 + 1) * 100;  // step, not to change too frequently
  const gr = getResponsiveGroup(ws.w);
  const cell_w = round(mw[gr]/12);  // w/10
  const cell_h = round(cell_w * 1.1);

  /*
  const items = [];
  for (let i=0; i < N; i++) {
    items.push(1+i);
  }
  */

  return (
    <div className="App" style={{height: ws.h + 'px'}}>
      <BackDrop width={mw[gr]} height={mh} />
      <Card3D items={sekki.s24} hue={sekki_hue} cellWidth={cell_w} cellHeight={cell_h} />
      <DebugOut row={0} color="yellow">SpacetimeQ: Debug Version</DebugOut>
      <DebugOut row={1} color="white">{ws.w}&nbsp;x&nbsp;{ws.h}&nbsp;({gr})</DebugOut>
    </div>
  ); // sekki.s24
}

const DebugOut = props =>
  <p className="debug-out" style={{ top: props.row * 20 + 'px', color: props.color }}>
    {props.children}
  </p>;

export default App;

